import './FrontLayoutChat.css'
import { useEffect, useState } from 'react'
import { getDatabase, push, ref, set, onChildAdded } from "firebase/database";
import { isDocument } from '@testing-library/user-event/dist/utils';
export function FrontLayoutChat() {


    const [name, setname] = useState("")

    const [livemessage, setlivemessage] = useState("")

    const [chat, setchat] = useState([


    ])

    const db = getDatabase();
    const chatList = ref(db, 'allChat');

    const scldwnalwys = () => {

        const el = document.getElementById('chat');

        if (el) {
            el.scrollTop = el.scrollHeight;
        }
    }

    useEffect(() => {
        onChildAdded(chatList, (data) => {

            setchat((chat) => [...chat, data.val()])


            setTimeout(() => {
                scldwnalwys()
            }, 500)


        }

        );





    }, [])


    const messagetransfer = () => {
        const chatbox = push(chatList);
        set(chatbox, {
            "name": name, "message": livemessage
        });


        // const c = [...chat]

        // c.push({ "name": name, "message": livemessage })
        // setchat(c)


        setlivemessage("")

    }

    return (

        <div className="interface">

            {name ? null : <input type="text" placeholder='Enter Name  To Continue ' onBlur={(e) => setname(e.target.value)} />
            }


            {
                name ? <div > <h4> User:{name}    </h4>
                    <div id="chat" className="chat-section mb-5   ">

                        {chat.map((c, i) =>
                            <div key={i} className={`box ${c.name === name ? "me" : " "}   `}     >
                                <p className='chat-box '>
                                    <strong> {c.name}: </strong>
                                    <span>  {c.message}  </span>
                                </p>

                            </div>)
                        }

                        {/* Sending Data To Firstly State  Than fs */}

                    </div>
                    <div className="send-btn    ">

                        <input type="text" placeholder=' Hey!  Type Your Cute Message' onChange={(e) => setlivemessage(e.target.value)}

                            value={livemessage}

                        />
                        <button type="submit" onClick={messagetransfer}    >Send</button>

                    </div>
                </div> : null
            }






        </div>



    )

}
